import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { Subscription } from 'rxjs';
import {
  GetAcademicProgramsSuggestions,
  SetCurrentAcademicProgram,
} from '../@store/academic-programs/academic-programs.actions';
import { AcademicProgramsState } from '../@store/academic-programs/academic-programs.state';
import { IAcademicProgram } from '../@store/academic-programs/academic-programs.model';

const MAX_SUGGESTIONS_ITEMS = 5;

@Component({
  selector: 'app-academics',
  templateUrl: './academics.page.html',
  styleUrls: ['./academics.page.scss'],
})
export class AcademicsPage implements OnInit, OnDestroy {
  public items = [];
  public maxSuggestions = MAX_SUGGESTIONS_ITEMS;
  private suggestionsSubscription: Subscription;

  constructor(private router: Router, private store: Store) {}

  ngOnInit() {
    this.subscribeToSuggestions();
  }

  public ionViewWillEnter() {
    if (this.store.selectSnapshot(AcademicProgramsState.getAcademicProgramsSuggestions).length === 0) {
      this.store.dispatch(new GetAcademicProgramsSuggestions());
    }
  }

  public doRefresh(event): void {
    const subscription = this.store.dispatch(new GetAcademicProgramsSuggestions()).subscribe(
      () => {
        subscription.unsubscribe();
        event.target.complete();
      },
      (error) => {
        subscription.unsubscribe();
        event.target.complete();
      }
    );
  }

  private subscribeToSuggestions() {
    this.suggestionsSubscription = this.store
      .select(AcademicProgramsState.getAcademicProgramsSuggestions)
      .subscribe((res) => {
        this.items = res;
      });
  }
  public goDetail(): void {
    this.router.navigate(['/detail']);
  }

  public navigateTo(url) {
    this.router.navigate([url]);
  }

  public filter() {}

  public onItemClick($event) {
    const program = this.store
      .selectSnapshot(AcademicProgramsState.getAcademicProgramsSuggestions)
      .find((item) => item.id === $event);
    const programUpdated: IAcademicProgram = { ...program, own: false } as IAcademicProgram;
    this.store.dispatch(new SetCurrentAcademicProgram(programUpdated));
    this.router.navigate(['academic-program-detail', $event]);
  }

  private disconnectObservers(): void {
    try {
      this.suggestionsSubscription.unsubscribe();
    } catch (error) {
      console.error(error);
    }
  }

  ngOnDestroy() {
    this.disconnectObservers();
  }
}
